import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import yuangongList from '@/views/pages/yuangong/list'
import yuangongDetail from '@/views/pages/yuangong/formModel'
import yuangongAdd from '@/views/pages/yuangong/formAdd'
import yuangongCenter from '@/views/pages/yuangong/center'
import xiangmujingliList from '@/views/pages/xiangmujingli/list'
import xiangmujingliDetail from '@/views/pages/xiangmujingli/formModel'
import xiangmujingliAdd from '@/views/pages/xiangmujingli/formAdd'
import xiangmujingliCenter from '@/views/pages/xiangmujingli/center'
import xiangmuxinxiList from '@/views/pages/xiangmuxinxi/list'
import xiangmuxinxiDetail from '@/views/pages/xiangmuxinxi/formModel'
import xiangmuxinxiAdd from '@/views/pages/xiangmuxinxi/formAdd'
import renwufenpeiList from '@/views/pages/renwufenpei/list'
import renwufenpeiDetail from '@/views/pages/renwufenpei/formModel'
import renwufenpeiAdd from '@/views/pages/renwufenpei/formAdd'
import xiangmujinduList from '@/views/pages/xiangmujindu/list'
import xiangmujinduDetail from '@/views/pages/xiangmujindu/formModel'
import xiangmujinduAdd from '@/views/pages/xiangmujindu/formAdd'
import chengbenyusuanList from '@/views/pages/chengbenyusuan/list'
import chengbenyusuanDetail from '@/views/pages/chengbenyusuan/formModel'
import chengbenyusuanAdd from '@/views/pages/chengbenyusuan/formAdd'
import xiangmugoutongList from '@/views/pages/xiangmugoutong/list'
import xiangmugoutongDetail from '@/views/pages/xiangmugoutong/formModel'
import xiangmugoutongAdd from '@/views/pages/xiangmugoutong/formAdd'
import goutonghuifuList from '@/views/pages/goutonghuifu/list'
import goutonghuifuDetail from '@/views/pages/goutonghuifu/formModel'
import goutonghuifuAdd from '@/views/pages/goutonghuifu/formAdd'
import yuangongjixiaoList from '@/views/pages/yuangongjixiao/list'
import yuangongjixiaoDetail from '@/views/pages/yuangongjixiao/formModel'
import yuangongjixiaoAdd from '@/views/pages/yuangongjixiao/formAdd'
import aboutusList from '@/views/pages/aboutus/list'
import aboutusDetail from '@/views/pages/aboutus/formModel'
import aboutusAdd from '@/views/pages/aboutus/formAdd'
import newsList from '@/views/pages/news/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yuangongList',
			component: yuangongList
		}, {
			path: 'yuangongDetail',
			component: yuangongDetail
		}, {
			path: 'yuangongAdd',
			component: yuangongAdd
		}
		, {
			path: 'yuangongCenter',
			component: yuangongCenter
		}
		, {
			path: 'xiangmujingliList',
			component: xiangmujingliList
		}, {
			path: 'xiangmujingliDetail',
			component: xiangmujingliDetail
		}, {
			path: 'xiangmujingliAdd',
			component: xiangmujingliAdd
		}
		, {
			path: 'xiangmujingliCenter',
			component: xiangmujingliCenter
		}
		, {
			path: 'xiangmuxinxiList',
			component: xiangmuxinxiList
		}, {
			path: 'xiangmuxinxiDetail',
			component: xiangmuxinxiDetail
		}, {
			path: 'xiangmuxinxiAdd',
			component: xiangmuxinxiAdd
		}
		, {
			path: 'renwufenpeiList',
			component: renwufenpeiList
		}, {
			path: 'renwufenpeiDetail',
			component: renwufenpeiDetail
		}, {
			path: 'renwufenpeiAdd',
			component: renwufenpeiAdd
		}
		, {
			path: 'xiangmujinduList',
			component: xiangmujinduList
		}, {
			path: 'xiangmujinduDetail',
			component: xiangmujinduDetail
		}, {
			path: 'xiangmujinduAdd',
			component: xiangmujinduAdd
		}
		, {
			path: 'chengbenyusuanList',
			component: chengbenyusuanList
		}, {
			path: 'chengbenyusuanDetail',
			component: chengbenyusuanDetail
		}, {
			path: 'chengbenyusuanAdd',
			component: chengbenyusuanAdd
		}
		, {
			path: 'xiangmugoutongList',
			component: xiangmugoutongList
		}, {
			path: 'xiangmugoutongDetail',
			component: xiangmugoutongDetail
		}, {
			path: 'xiangmugoutongAdd',
			component: xiangmugoutongAdd
		}
		, {
			path: 'goutonghuifuList',
			component: goutonghuifuList
		}, {
			path: 'goutonghuifuDetail',
			component: goutonghuifuDetail
		}, {
			path: 'goutonghuifuAdd',
			component: goutonghuifuAdd
		}
		, {
			path: 'yuangongjixiaoList',
			component: yuangongjixiaoList
		}, {
			path: 'yuangongjixiaoDetail',
			component: yuangongjixiaoDetail
		}, {
			path: 'yuangongjixiaoAdd',
			component: yuangongjixiaoAdd
		}
		, {
			path: 'aboutusList',
			component: aboutusList
		}, {
			path: 'aboutusDetail',
			component: aboutusDetail
		}, {
			path: 'aboutusAdd',
			component: aboutusAdd
		}
		, {
			path: 'newsList',
			component: newsList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
