
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0 18%","margin":"20px auto 60px","color":"#666","background":"#fff","width":"100%","fontSize":"14px","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator="/" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }"></el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="24">
					<el-form-item label="" prop="xiangmubianhao">
						<el-input class="list_inp" v-model="form.xiangmubianhao" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.xiangmubianhao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="xiangmumingcheng">
						<el-input class="list_inp" v-model="form.xiangmumingcheng" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.xiangmumingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="riqi">
						<el-date-picker
							class="list_date"
							v-model="form.riqi"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.riqi?true:false"
							placeholder="" />
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="jinglizhanghao">
						<el-input class="list_inp" v-model="form.jinglizhanghao" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.jinglizhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="jinglixingming">
						<el-input class="list_inp" v-model="form.jinglixingming" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.jinglixingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="huifushijian">
						<el-date-picker
							class="list_date"
							v-model="form.huifushijian"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.huifushijian?true:false"
							placeholder="" />
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="goutongneirong">
						<el-input v-model="form.goutongneirong" placeholder="" type="textarea"
						:readonly="!isAdd||disabledForm.goutongneirong?true:false"
						/>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="" prop="huifuneirong">
						<el-input v-model="form.huifuneirong" placeholder="" type="textarea"
						:readonly="!isAdd||disabledForm.huifuneirong?true:false"
						/>
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick"></el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//
	const tableName = 'goutonghuifu'
	const formName = ''
	//
	const breadList = ref([{
		name: formName
	}])
	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form
	const form = ref({
		xiangmubianhao: '',
		xiangmumingcheng: '',
		riqi: '',
		goutongneirong: '',
		jinglizhanghao: '',
		jinglixingming: '',
		huifuneirong: '',
		huifushijian: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		xiangmubianhao : false,
		xiangmumingcheng : false,
		riqi : false,
		goutongneirong : false,
		jinglizhanghao : false,
		jinglixingming : false,
		huifuneirong : false,
		huifushijian : false,
	})
	const isAdd = ref(false)
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		xiangmubianhao: [
		],
		xiangmumingcheng: [
		],
		riqi: [
		],
		goutongneirong: [
		],
		jinglizhanghao: [
		],
		jinglixingming: [
		],
		huifuneirong: [
		],
		huifushijian: [
		],
	})
	//methods

	//methods
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='xiangmubianhao'){
					form.value.xiangmubianhao = row[x];
					disabledForm.value.xiangmubianhao = true;
					continue;
				}
				if(x=='xiangmumingcheng'){
					form.value.xiangmumingcheng = row[x];
					disabledForm.value.xiangmumingcheng = true;
					continue;
				}
				if(x=='riqi'){
					form.value.riqi = row[x];
					disabledForm.value.riqi = true;
					continue;
				}
				if(x=='goutongneirong'){
					form.value.goutongneirong = row[x];
					disabledForm.value.goutongneirong = true;
					continue;
				}
				if(x=='jinglizhanghao'){
					form.value.jinglizhanghao = row[x];
					disabledForm.value.jinglizhanghao = true;
					continue;
				}
				if(x=='jinglixingming'){
					form.value.jinglixingming = row[x];
					disabledForm.value.jinglixingming = true;
					continue;
				}
				if(x=='huifuneirong'){
					form.value.huifuneirong = row[x];
					disabledForm.value.huifuneirong = true;
					continue;
				}
				if(x=='huifushijian'){
					form.value.huifushijian = row[x];
					disabledForm.value.huifushijian = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('jinglizhanghao') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.jinglizhanghao = json.jinglizhanghao
				disabledForm.value.jinglizhanghao = true;
			}
			if(json.hasOwnProperty('jinglixingming') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.jinglixingming = json.jinglixingming
				disabledForm.value.jinglixingming = true;
			}
		})
	}
	//
	//
	const backClick = () => {
		history.back()
	}
	//
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(``,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(``,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 
	.bread_view {
		border: 1px solid #ddd;
		border-radius: 0px;
		padding: 12px 20px;
		margin: 10px auto 10px;
		background: #f9f9f9;
		width: 100%;
		position: relative;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #ccc;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #999;
					display: inline-block;
				}
			}
		}
	}
	// 
	.add_form{
		border: 1px solid #0058ab20;
		border-radius: 0px;
		padding: 30px 7%;
		background: #0058ab02;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 0 0 20px;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			border-color: #0058ab20;
			border-width: 0 0 1px;
			border-style: dashed;
			//label
			.el-form-item__label {
			 color: inherit;
			 background: none;
			 font-weight: 500;
			 display: block;
			 width: auto;
			 font-size: inherit;
			 text-align: right;
			 min-width: 150px;
			}
			// 
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 
				.list_inp {
					border: 1px solid #0058ab30;
					padding: 0 10px;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 350px;
					height: 36px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//
				.list_date {
					border: 1px solid #0058ab30;
					border-radius: 0;
					background: #fff;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 300px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 
				.el-textarea__inner {
					border: 1px solid #0058ab30;
					border-radius: 0px;
					padding: 12px;
					color: #666;
					width: 100%;
					font-size: 14px;
					min-height: 120px;
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		padding: 0 150px;
		margin: 0 0 20px;
		display: flex;
		width: 100%;
		align-items: center;
		.formModel_cancel {
			border: 0;
			cursor: pointer;
			border-radius: 0px;
			padding: 0 24px;
			margin: 0 20px 0 0;
			outline: none;
			color: #333;
			background: #0058ab10;
			width: auto;
			font-size: 14px;
			height: 36px;
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
			border: 0;
			cursor: pointer;
			border-radius: 0px;
			padding: 0 24px;
			margin: 0 20px 0 0;
			outline: none;
			color: #333;
			background: #0058ab30;
			width: auto;
			font-size: 14px;
			height: 36px;
		}
		.formModel_confirm:hover {
		}
	}
</style>